header {
	width: 100%;
	height: 120px;

	.inner {
		width: 100%;
		height: 100%;
		background: #ee7d31;
		position: relative;

		h1 {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 12%;
			height: 100%;
			background: #fff;
			float: left;

			a {
				.fa-angrycreative {
					color: #ee7d31;
					font-size: 80px;
					line-height: 120px;
				}
			}
		}

		#gnb {
			float: left;
			width: 88%;
			height: 100%;
			display: flex;
			align-items: center;

			li {
				a {
					color: #fff;
					font: 500 14px/1 'Montserrat';
					padding: 0 45px;
					position: relative;

					&:hover::after {
						content: '';
						display: block;
						width: 50%;
						height: 2px;
						background-color: rgba(255, 255, 255, 0.5);
						position: absolute;
						bottom: -5px;
						left: 50%;
						transform: translateX(-50%);
					}
				}
			}
		}

		.fa-bars {
			position: absolute;
			right: 50px;
			top: 50%;
			transform: translateY(-50%);
			color: #fff;
		}
	}
}
